<template>
<div class="carousel slide" data-ride="carousel">
  <!-- Indicators -->
  <ol class="carousel-indicators" v-show="indicators">
    <indicator></indicator>
  </ol>
  <!-- Wrapper for slides -->
  <div class="carousel-inner" role="listbox">
    <slot></slot>
  </div>
  <!-- Controls -->
  <a v-show="controls.left" class="left carousel-control" @click="prevClick">
    <span class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
    <span class="sr-only">Previous</span>
  </a>
  <a v-bind:disabled="controls.right" class="right ad-carousel-control" @click="nextClick">
    <span>{{adTime}}<span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span></span>
    <span class="sr-only">Next</span>
  </a>
</div>
</template>

<script>
import EventListener from './utils/EventListener.js'
export default {
  props: {
    indicators: {
      type: Boolean,
      default: true
    },
    controls: {
      type: Object,
      default: function () {
        return {
          left: false,
          right: false
        }
      }
    },
    interval: {
      type: Array,
      default: function () {
        return [0]
      }
    },
    adIds: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  events: {
    'swipe-left': function () {
      this.nextClick()
    }
  },
  components: {
    'indicator': {
      inherit: true,
      template: '<li v-for="i in indicator" @click="handleIndicatorClick($index)" v-bind:class="{\'active\':$index === activeIndex}"</li>',
      methods: {
        handleIndicatorClick (index) {
          if (this.isAnimating) return false
          this.isAnimating = true
          this.activeIndex = index
        }
      }
    }
  },
  data () {
    return {
      indicator: [],
      activeIndex: 0,
      isAnimating: false,
      intervalTime: 0,
      isFinish: false,
      intervalId: null
    }
  },
  computed: {
    slider () {
      return this.$el.querySelectorAll('.item')
    },
    adTime () {
      let result = '' + this.intervalTime + ''
      if (this.intervalTime <= 0 || this.isFinish) {
        if (this.intervalId) {
          clearInterval(this.intervalId)
        }
        result = ''
      }
      return result
    }
  },
  watch: {
    'activeIndex': {
      handler: function (newVal, oldVal) {
        newVal > oldVal ? this.slide('left', newVal, oldVal) : this.slide('right', newVal, oldVal)
      }
    }, 'interval': {
      handler: function (val, oldVal) {
        if (val && val[0]) {
          this.timeoutManager(this.showNext, this.interval[0])
          const selectedEl = this.slider[0]
          selectedEl.classList.add('active')
        }
      },
      deep: true
    }
  },
  methods: {
    slide (direction, selected, prev) {
      if (this._prevSelectedEvent) this._prevSelectedEvent.remove()
      if (this._selectedEvent) this._selectedEvent.remove()

      const prevSelectedEl = this.slider[prev]
      const selectedEl = this.slider[selected]
      const transitionendFn = () => {
        [...this.slider].forEach((el) => { el.className = 'item' })
        selectedEl.classList.add('active')
        this.isAnimating = false
      }

      direction === 'left' ? selectedEl.classList.add('next') : selectedEl.classList.add('prev')
      // request property that requires layout to force a layout
      // var x = selectedEl.clientHeight
      this._prevSelectedEvent = EventListener.listen(prevSelectedEl, 'transitionend', transitionendFn)
      this._selectedEvent = EventListener.listen(selectedEl, 'transitionend', transitionendFn)
      prevSelectedEl.classList.add(direction)
      selectedEl.classList.add(direction)
    },
    nextClick () {
      if ((this.isAnimating || !this.controls.right) && !this.isFinish) {
        return false
      }
      this.isAnimating = true
      this.activeIndex + 1 < this.slider.length ? this.activeIndex ++ : this.activeIndex = 0
      this.controls.right = false
      this.timeoutManager(this.showNext, this.interval[this.activeIndex])
    },
    showNext () {
      if (this.controls.right) return false
      if (this.activeIndex + 1 === this.slider.length) {
        this.$dispatch('enable-online')
        this.isFinish = true
      }
      this.$dispatch('view-ads', this.adIds[this.activeIndex])
      if (this.activeIndex + 1 === this.slider.length) {
        this.$dispatch('enable-online')
      }
      this.controls.right = true
    },
    prevClick () {
      if (this.isAnimating) return false
      this.isAnimating = true
      this.activeIndex === 0 ? this.activeIndex = this.slider.length - 1 : this.activeIndex -= 1
    },
    timeoutManager (func, time) {
      let ts = this
      setTimeout(func, time)
      this.intervalTime = time / 1000
      this.intervalId = setInterval(function () {
        ts.intervalTime--
      }, 1000)
    }
  },
  ready () {

  }

}
</script>

<style scoped>
  .carousel-control {
    cursor: pointer;
  }
</style>



