import Carousel from '../vue-strap/Carousel'
import Slider from '../vue-strap/Slider'

export default {
  props: ['adResource'],
  computed: {
    interval: function () {
      var intervals = []
      for (let i = this.adResource.length - 1; i >= 0; i--) {
        intervals.push(parseInt(this.adResource[i].ADVERT_PLAY_TIME) * 1000)
      }
      return intervals
    },
    adIds: function () {
      var adIds = []
      for (let i = this.adResource.length - 1; i >= 0; i--) {
        adIds.push(this.adResource[i].ADVERT_ID)
      }
      return adIds
    }
  },
  events: {
    'view-ads': function (adId) {
      //  
      this.$http.jsonp('http://58.23.16.163:8088/portMgt/backstageJson/Advert_addAdvertPlayRecord.do?pwsId=999&macAddress=123&advertIds=' + adId)
    }
  },
  components: {
    Carousel,
    Slider
  },
  methods: {
    adClick: function (ad) {
      //  
      this.$http.jsonp('http://58.23.16.163:8088/portMgt/backstageJson/Advert_clickAdvertRecord.do?advertPlayRecordId=' + ad.ADVERT_ID)
      let url = ad.LINK_URL
      if (url.indexOf('http://') !== 0) {
        url = 'http://' + url
      }
      window.open(url, '_blank');
    },
    onSwipeLeft: function () {
      console.log('swipe-left')
      this.$broadcast('swipe-left')
    }

  }
}




